import React, { useRef, useState } from "react";
import { Button, Dropdown, DropdownButton, Form } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import { AiFillEye } from "react-icons/ai";
import { BsThreeDotsVertical } from "react-icons/bs";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { FaEdit } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import paginationFactory from "react-bootstrap-table2-paginator";
import { ConfirmationModal } from "../../../../../../components/Modal/ConfirmationModal";
import {
  deletePatientPres,
  getPresMedicine,
} from "../../../../../../app/features/patientPrescription/patientPrescription.slice";
import PatientPresModal from "./PresModal";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrescriptionPDF from "../../../../../../components/Theme/PrescriptionPDF";

function rowStyle() {
  return { backgroundColor: "#F8F8F8" };
}

export default function PrescriptionModule({
  // slotDescription,
  showPresToPatient,
  setShowPresToPatient,
  setPrescriptionIdToPatient,
}) {
  const dispatch = useDispatch();
  const PreMedicineData = useSelector(
    (state) => state?.patientPrescription?.PreMedicineData
  );
  const slotDescription = JSON.parse(localStorage.getItem('slotDescription'));
  const showPrescription = useSelector((state) => state?.patientPrescription);
  const printButtonRef = useRef(null);
  const [presModal, setPresModal] = useState({
    data: null,
    show: false,
  });
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [showMedicine, setShowMedicine] = useState({ data: null, show: false });
  const [deleteId, setDeleteId] = useState();

  const columns = [
    {
      dataField: "medicineName",
      text: "Medicine Name",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Medicine Name</p>
          <p className="table-custom-data">{cell?.medicineName}</p>
        </>
      ),
    },
    {
      dataField: "doseFrequency",
      text: "Dose Frequency",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose Frequency</p>
          <p className="table-custom-data">{cell?.doseFrequency}</p>
        </>
      ),
    },
    {
      dataField: "dose",
      text: "Dose",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose</p>
          <p className="table-custom-data">{cell?.dose}</p>
        </>
      ),
    },
    {
      dataField: "formulation",
      text: "Formulation",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Formulation</p>
          <p className="table-custom-data">{cell?.formulation}</p>
        </>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      formatter: (row, cell) => {
        return (
          <>
            <DropdownButton
              // popperConfig={{ strategy: "fixed" }}
              id="dropdown-item-button"
              title={
                <BsThreeDotsVertical
                  className="float-right color-99"
                  size={25}
                />
              }
              size="sm"
              bsPrefix="moreIcon"
              className="moreIcon edit-dropdown"
              direction="left"
            >
              <div className="set-dropdown-menu">
                <Dropdown.Item
                  as="button"
                  className="d-flex align-items-center"
                  onClick={() => setShowMedicine({ data: cell, show: true })}
                >
                  <AiFillEye size={18} style={{ color: "#1A1A1A" }} />
                  <span
                    className="px-2"
                    style={{ fontSize: "14px", color: "#1A1A1A" }}
                  >
                    View
                  </span>
                </Dropdown.Item>
                <Dropdown.Item
                  as="button"
                  className="d-flex align-items-center"
                  onClick={() => setPresModal({ data: cell, show: true })}
                >
                  <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                  <span
                    className="px-2"
                    style={{ fontSize: "14px", color: "#1A1A1A" }}
                  >
                    Edit
                  </span>
                </Dropdown.Item>
                <Dropdown.Item
                  as="button"
                  className="d-flex align-items-center"
                  onClick={() => {
                    setDeleteId(cell);
                    setShowDeleteModal(true);
                  }}
                >
                  <RiDeleteBin5Fill size={18} style={{ color: "#FD2121" }} />
                  <span
                    className="px-2"
                    style={{ fontSize: "14px", color: "#FD2121" }}
                  >
                    Delete
                  </span>
                </Dropdown.Item>
              </div>
            </DropdownButton>
          </>
        );
      },
      headerAttrs: {
        hidden: true,
      },
    },
  ];

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPresMedicine({ finalData }));
  }, [dispatch, slotDescription?.appointmentId]);


  function onCreateSuccess() {
    let finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPresMedicine({ finalData }));
  }

  function deletePres() {
    const finalData = {
      medicineId: deleteId?.medicineId,
    };
    dispatch(deletePatientPres({ finalData, onCreateSuccess }));
  }

  // let newVariable = [];
  // getAllPrescription?.data?.map((data) =>
  //   data.medicineList.map((item) => newVariable.push(item))
  // );

  let newVariable = [];

  if (PreMedicineData?.medicineList) {
    PreMedicineData?.medicineList?.forEach((data) => {
      if (data?.medicineList) {
        newVariable.push(...data.medicineList);
      }
    });
  }
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 2,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "2",
        value: 2,
      },
    ],
  };

  return (
    <>
      {PreMedicineData?.medicineList?.length <= 0 || PreMedicineData?.medicineList === undefined ? (
        <div className="d-flex justify-content-center">
          <Button
            className="prescription-btn px-4 d-flex justify-content-center"
            onClick={() => setPresModal({ data: null, show: true })}
          >
            Create Prescription
          </Button>
        </div>
      ) : (
        <div className="custom-table-spacing">
          <Form>
            <div className="d-flex justify-content-between">
              <Form.Check
                type="checkbox"
                id="show-prescription"
                label="Show Prescription to Patient"
                onChange={() => {
                  setShowPresToPatient(!showPresToPatient);
                  setPrescriptionIdToPatient(
                    showPrescription?.PreMedicineData?.medicineList?.prescriptionId
                  );
                }}
              />
              <PrintComponents
                trigger={
                  <button
                    type="button"
                    ref={printButtonRef}
                    className="border-0 px-3 py-2 rounded-2"
                  >
                    <PiDownloadSimpleBold size={20} /> Download PDF
                  </button>
                }
              >
                <PrescriptionPDF prescriptionData={PreMedicineData}/>
              </PrintComponents>
            </div>
          </Form>
          <BootstrapTable
            keyField="medicineId"
            data={PreMedicineData?.medicineList}
            columns={columns}
            bordered={false}
            rowStyle={rowStyle}
            wrapperClasses="table-responsive"
            pagination={paginationFactory(paginateOptions)}
          />
          <p
            className="float-right text-cursor-pointer"
            style={{
              color: "#2269F2",
              fontWeght: 500,
              fontSize: "18px",
              cursor: "pointer",
            }}
            onClick={() => setPresModal({ data: null, show: true })}
          >
            + Add Prescription
          </p>
        </div>
      )}
      <PatientPresModal
        data={presModal.data}
        show={presModal.show}
        view={showMedicine.show}
        viewData={showMedicine.data}
        onCloseView={() => setShowMedicine({ show: false, data: null })}
        onClose={() => setPresModal({ show: false, data: null })}
        slotDescription={slotDescription}
      />
      <ConfirmationModal
        show={showDeleteModal}
        onHide={() => setShowDeleteModal(false)}
        heading={"Delete Prescription"}
        title={"the prescription"}
        removeFunc={deletePres}
      />
    </>
  );
}
